<template>
  <div class="parent">
    <header>
      <h1>Group Selector</h1>
    </header>
    <main>
      <div v-for="(group, index) in groups" class="card" :key="`group-${index}`">
        <h4>{{groups.indexOf(group) + 1}}</h4>
        <draggable class="list-group" :list="group" group="people">
          <div v-for="(person, index) in group" :key="`person-${index}`">{{person}}</div>
        </draggable>
      </div>
    </main>
    <aside>
      <div class="unselected">
        <h4>Unselected</h4>
        <p>Names, separated by commas</p>
        <form action="GET">
          <input type="text" v-model="namesInput">
          <button type="submit" @click.prevent="addToUnselected">Add names</button>
        </form>
        <draggable class="list-group" :list="dataUnselected" group="people">
          <div v-for="(person, index) in dataUnselected" :key="`person-${index}`">{{person}}</div>
        </draggable>
      </div>
    </aside>
  </div>
</template>

<script>
import draggable from 'vuedraggable';
export default {
  name: 'Draggable',
  components: {
    draggable,
  },
  data: () => {
    return {
      namesInput: '',
      dataUnselected: [],
      groups: (() => {
        let arr = [];
        for (let i = 0; i < 12; i++) {
          arr.push([]);
        }
        return arr;
      })(),
    };
  },
  methods: {
    addToUnselected: function() {
      const newNames = this.namesInput.split(',').map(name => name.trim());
      this.dataUnselected = [...this.dataUnselected, ...newNames];
      this.namesInput = '';
    },
  },
};
</script>

